"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { UsersIcon, VideoIcon } from "lucide-react";

import {
  SidebarGroup,
  SidebarGroupContent,
  SidebarMenu,
  SidebarMenuButton,
  SidebarMenuItem,
  useSidebar,
} from "@acme/ui/sidebar";

const items = [
  {
    title: "Videos",
    href: "/",
    icon: VideoIcon,
    isRootPath: true,
  },
  {
    title: "Members",
    href: "/members",
    icon: UsersIcon,
    isRootPath: false,
  },
];

export default function NavMain() {
  const pathname = usePathname();
  const { state } = useSidebar();
  const isCollapsed = state === "collapsed";

  // Extract workspace from pathname
  const workspaceMatch = pathname.match(/\/([^/]+)/);
  const workspace = workspaceMatch ? workspaceMatch[1] : "";

  return (
    <SidebarGroup>
      <SidebarGroupContent>
        <SidebarMenu>
          {items.map((item) => {
            // For root paths, use the item.href directly, otherwise prepend workspace
            const itemHref = item.isRootPath
              ? item.href
              : `/${workspace}${item.href}`;
            const isActive =
              pathname === itemHref ||
              (pathname.includes(item.href) && !item.isRootPath);

            return (
              <SidebarMenuItem key={item.title}>
                <SidebarMenuButton asChild>
                  <Link
                    href={itemHref}
                    className={`flex items-center gap-2 px-2 py-1 ${
                      isActive ? "text-primary outline outline-primary" : ""
                    }`}
                  >
                    <item.icon className="h-4 w-4" />
                    {!isCollapsed && <span>{item.title}</span>}
                  </Link>
                </SidebarMenuButton>
              </SidebarMenuItem>
            );
          })}
        </SidebarMenu>
      </SidebarGroupContent>
    </SidebarGroup>
  );
}
